import Bmob from "hydrogen-js-sdk";
let that;
let uid;
export default {
	data() {
		return {
			detail:[]
		}
	},
	
	onLoad() {
		that = this;
		uni.removeStorageSync("operationDet")
		uid = uni.getStorageSync("uid")
	},
	
	onShow() {
		that.getNoticeList()
	},
	
	methods: {
		gotodetail(item){
			that.readNotice(item)
			uni.setStorageSync("operationDet",item.detail)
			uni.navigateTo({
				url:"Infodetail/Infodetail"
			})
		},
		
		//点击改变消息状态
		readNotice(item){
			const query = Bmob.Query('InfoCenter');
			query.get(item.objectId).then(res => {
			  console.log(res)
			  res.set('status',true)
			  res.save()
				that.getNoticeList()
			}).catch(err => {
			  console.log(err)
			})
		},
		
		//得到消息列表
		getNoticeList(){
			const query = Bmob.Query("InfoCenter");
			query.order("-createdAt");
			const query1 = query.equalTo("toUser", '==',uid);
			const query2 = query.equalTo("fromUser", '==',"My5CIpNi");//这是系统消息
			
			query.or(query1, query2);
			query.find().then(res => {
				that.detail = res
			   console.log(res)
			});
		},
		
	}
}
